import { useState } from 'react'

import { Box, Grid, Typography, useMediaQuery, useTheme } from '@mui/material'

import ConferencePreviewCard from 'src/components/ConferencePreviewCard'
import NavigationArrows from 'src/components/NavigationArrows'
import SuspenseLoader from 'src/components/SuspenseLoader'

import { useGetRecentConferenceRounds } from 'src/hooks/api/clubConferenceHooks'

const RecentConferences = () => {
  const theme = useTheme()
  const [currentPage, setCurrentPage] = useState(0)

  // 최근 활동 데이터 가져오기
  const { data: conferences, isLoading } = useGetRecentConferenceRounds(12)

  // 화면 크기에 따라 itemsPerPage 계산
  const isXs = useMediaQuery(theme.breakpoints.down('sm')) // < 600px
  const isSm = useMediaQuery(theme.breakpoints.down('md')) // < 960px
  const itemsPerPage = isXs ? 1 : isSm ? 2 : 3

  const totalPages = Math.ceil((conferences?.length ?? 0) / itemsPerPage)
  const startIndex = currentPage * itemsPerPage
  const endIndex = startIndex + itemsPerPage
  const currentConferences = conferences?.slice(startIndex, endIndex) ?? []

  const handlePrevPage = () => {
    setCurrentPage(prev => (prev > 0 ? prev - 1 : totalPages - 1))
  }

  const handleNextPage = () => {
    setCurrentPage(prev => (prev < totalPages - 1 ? prev + 1 : 0))
  }

  if (isLoading) {
    return <SuspenseLoader />
  }

  if (!conferences || conferences.length === 0) {
    return null
  }

  return (
    <Box
      component="section"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: theme.spacing(3, 0),
        padding: theme.spacing(5, 4),
        [theme.breakpoints.down('sm')]: {
          padding: theme.spacing(3, 2)
        }
      }}
    >
      {/* 헤더 섹션 */}
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          width: '100%',
          maxWidth: '1220px'
        }}
      >
        <Box>
          <Typography
            variant="h3"
            sx={{
              fontWeight: 700,
              color: 'text.primary',
              marginBottom: theme.spacing(1)
            }}
          >
            지금 활발한 동아리
          </Typography>
          <Typography
            variant="body1"
            sx={{
              color: 'text.secondary'
            }}
          >
            지금 활발하게 활동하고 있는 동아리들의 활동을 살펴보세요
          </Typography>
        </Box>

        {/* 네비게이션 화살표 */}
        <NavigationArrows onPrevClick={handlePrevPage} onNextClick={handleNextPage} visible={totalPages > 1} />
      </Box>

      {/* 카드 그리드 */}
      <Box sx={{ width: '100%', maxWidth: '1220px' }}>
        <Grid container spacing={3}>
          {currentConferences.map((conference, index) => (
            <Grid item xs={12} sm={6} md={4} key={`${conference.conference.club.id}-${conference.id}-${index}`}>
              <ConferencePreviewCard
                club={{
                  id: conference.conference.club.id,
                  name: conference.conference.club.name,
                  description: conference.conference.club.description
                }}
                conferenceRound={{
                  id: conference.id,
                  round: conference.round,
                  startAt: conference.startAt,
                  note: conference.note,
                  attendedUserCount: 0,
                  absentUserCount: 0,
                  isPublic: true,
                  conferenceTitle: conference.conference.title,
                  categories: conference.conference.categories
                }}
              />
            </Grid>
          ))}
        </Grid>
      </Box>
    </Box>
  )
}

export default RecentConferences
