import { ChevronLeft, ChevronRight } from '@mui/icons-material'
import { Box, IconButton } from '@mui/material'

interface NavigationArrowsProps {
  onPrevClick: () => void
  onNextClick: () => void
  visible?: boolean
}

const NavigationArrows = ({ onPrevClick, onNextClick, visible = true }: NavigationArrowsProps) => {
  if (!visible) return null

  return (
    <Box sx={{ display: 'flex', gap: 1 }}>
      <IconButton
        onClick={onPrevClick}
        sx={{
          border: '1px solid',
          borderColor: 'divider',
          borderRadius: '50%',
          width: 40,
          height: 40,
          '&:hover': {
            backgroundColor: 'action.hover'
          }
        }}
      >
        <ChevronLeft />
      </IconButton>
      <IconButton
        onClick={onNextClick}
        sx={{
          border: '1px solid',
          borderColor: 'divider',
          borderRadius: '50%',
          width: 40,
          height: 40,
          '&:hover': {
            backgroundColor: 'action.hover'
          }
        }}
      >
        <ChevronRight />
      </IconButton>
    </Box>
  )
}

export default NavigationArrows
